/*-----------------------------------------------
 __maintenace
-----------------------------------------------*/

.maintenace {
    overflow: hidden;
    text-align: center;
    color: #000;
    @include media(767px) {
        text-align: justify;
    }
    div {
        position: fixed;
        top: 50%;
        left: 50%;
        z-index: 99999999;
        font-size: 16px;
        transform: translate(-50%,-50%);
        display: block;
        width: 80%;
        @include media(767px) {
            font-size: 14px;
            br {
                display: none;
            }
        }
        p {
            &:last-child {
                text-align: center!important;
            }
        }
    }
    &:before {
        content: '';
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-image: url(../images/mv_01.jpg);
        background-position: center center;
        background-size: cover;
        z-index: 999999;
    }
    &:after {
        content: '';
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100vh;
        background-color: rgba(255,255,255,0.7);
        z-index: 9999999;
    }

}
.wrapper {
    position: relative;
}

.fa {padding-right: .5em;}
.dnon {display: none;}

.l_row {
    max-width: 1400px;
    width: 100%;
    margin: 0 auto;
    &.pd_side {
        padding: 0 5%;
    }
    &.pd_sideItem {
        padding: 0 7%;
        @include media(1200px) {
            padding-left: 5%;
            padding-right: 5%;
        }
        @include media(480px) {
            padding-left: 9%;
            padding-right: 9%;
        }
    }
}

.contents_wrapper {
}


.md_arrow {
    width: 100%;
    height: 50px;
    position: relative;
    transition: all 0.1s ease-in;
    display: block;
    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        margin-left: -1px;
        background: #000;
        width: 2px;
        height: 100%;
    }
    &:after {
        content: '';
        position: absolute;
        top: 0;
        left: 50%;
        margin-left: -5px;
        background: url(../images/arrow_blk.svg) 0 0 no-repeat;
        width: 10px;
        height: 10px;
    }
    &.bottom {
        &:after {
            top: auto;
            bottom: 0;
            transform: rotate(180deg);
        }
    }
    &.glm {
        width: 60px;
    }
    &.left {
        width: auto;
        height: auto;
        &:hover {
            &:before {left: -10px;}
            &:after {left: -10px;}
        }
        &:before {
            top: 50%;
            margin-top: -1px;
            left: 0;
            margin-left: 0;
            width: 50px;
            height: 2px;
            transition: all .25s ease-out;
        }
        &:after {
            left: 0;
            top: 50%;
            margin-top: -5px;
            transform: rotate(-90deg);
            transition: all .25s ease-out;
        }
    }
    &.right {
        width: auto;
        height: auto;
        &:hover {
            &:before {left: -30px;}
            &:after {left: 20px;}
        }
        &:before {
            top: 50%;
            margin-top: -1px;
            left: -35px;
            margin-left: 0;
            width: 50px;
            height: 2px;
            transition: all .25s ease-out;
        }
        &:after {
            left: 15px;
            top: 50%;
            margin-top: -5px;
            transform: rotate(90deg);
            transition: all .25s ease-out;
        }
        &.short {
            &:before {
                left: 20px;
                width: 30px;
            }
            &:after {
                left: 55px;
            }
            &:hover {
                &:before {left: 40px;}
                &:after {left: 75px;}
            }
        }
        &.long {
            &:before {
                left: -35px;
                width: 70px;
            }
            &:after {
                left: 35px;
            }
            &:hover {
                &:before {left: -10px;}
                &:after {left: 60px;}
            }
        }
    }
    &.totop {
        transform: rotate(180deg);
        @media all and (-ms-high-contrast:none){
            left: -10px;
            top: -10px;
        }
    }
}
a.md_arrow__btn,.md_arrow__btn {
    position: relative;
    display: block;
    text-decoration: none;
    color: $base_font__color;
    display: inline-block;
    box-sizing: border-box;
    line-height: 1;
    white-space: nowrap;
    &:before {
        content: '';
        position: absolute;
        margin-top: -1px;
        bottom: -2px;
        left: 0;
        margin-left: 0;
        width: 100%;
        height: 2px;
        background: #000;
        transition: all 0.1s ease-in;
    }
    &:after {
        content: '';
        position: absolute;
        bottom: -5px;
        left: 100%;
        margin-left: -4px;
        background: url(../images/arrow_blk.svg) 0 0 no-repeat;
        width: 8px;
        height: 8px;
        transform: rotate(90deg);
        transition: all 0.1s ease-in;
    }
    &:hover {
        &:before {
            width: 110%;
        }
        &:after {
            left: 110%;
        }
    }
}

