.maintenace {
  overflow: hidden;
  text-align: center;
  color: #000;
}

@media screen and (max-width: 767px) {
  .maintenace {
    text-align: justify;
  }
}

.maintenace div {
  position: fixed;
  top: 50%;
  left: 50%;
  z-index: 99999999;
  font-size: 16px;
  transform: translate(-50%, -50%);
  display: block;
  width: 80%;
}

@media screen and (max-width: 767px) {
  .maintenace div {
    font-size: 14px;
  }
  .maintenace div br {
    display: none;
  }
}

.maintenace div p:last-child {
  text-align: center !important;
}

.maintenace:before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-image: url(../images/mv_01.jpg);
  background-position: center center;
  background-size: cover;
  z-index: 999999;
}

.maintenace:after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(255, 255, 255, 0.7);
  z-index: 9999999;
}

.wrapper {
  position: relative;
}

.fa {
  padding-right: .5em;
}

.dnon {
  display: none;
}

.l_row {
  max-width: 1400px;
  width: 100%;
