}

.l_row.pd_side {
  padding: 0 5%;
}

.l_row.pd_sideItem {
  padding: 0 7%;
}

@media screen and (max-width: 1200px) {
  .l_row.pd_sideItem {
    padding-left: 5%;
    padding-right: 5%;
  }
}

@media screen and (max-width: 480px) {
  .l_row.pd_sideItem {
    padding-left: 9%;
    padding-right: 9%;
  }
}

.md_arrow {
  width: 100%;
  height: 50px;
  position: relative;
  transition: all 0.1s ease-in;
  display: block;
}

.md_arrow:before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -1px;
  background: #000;
  width: 2px;
  height: 100%;
}

.md_arrow:after {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  margin-left: -5px;
  background: url(../images/arrow_blk.svg) 0 0 no-repeat;
  width: 10px;
  height: 10px;
}

.md_arrow.bottom:after {
  top: auto;
  bottom: 0;
  transform: rotate(180deg);
}

.md_arrow.glm {
  width: 60px;
}

.md_arrow.left {
  width: auto;
  height: auto;
}

.md_arrow.left:hover:before {
  left: -10px;
}

.md_arrow.left:hover:after {
  left: -10px;
}

.md_arrow.left:before {
  top: 50%;
  margin-top: -1px;
  left: 0;
  margin-left: 0;
  width: 50px;
  height: 2px;
  transition: all .25s ease-out;
}

.md_arrow.left:after {
  left: 0;
  top: 50%;
  margin-top: -5px;
  transform: rotate(-90deg);
  transition: all .25s ease-out;
}

.md_arrow.right {
  width: auto;
  height: auto;
}

.md_arrow.right:hover:before {
  left: -30px;
}

.md_arrow.right:hover:after {
  left: 20px;
}

.md_arrow.right:before {
  top: 50%;
  margin-top: -1px;
  left: -35px;
  margin-left: 0;
  width: 50px;
  height: 2px;
  transition: all .25s ease-out;
}

.md_arrow.right:after {
  left: 15px;
  top: 50%;
  margin-top: -5px;
  transform: rotate(90deg);
  transition: all .25s ease-out;
}

.md_arrow.right.short:before {
  left: 20px;
  width: 30px;
}

.md_arrow.right.short:after {
  left: 55px;
}

.md_arrow.right.short:hover:before {
  left: 40px;
}

.md_arrow.right.short:hover:after {
  left: 75px;
}

.md_arrow.right.long:before {
  left: -35px;
  width: 70px;
}

.md_arrow.right.long:after {
  left: 35px;
}

.md_arrow.right.long:hover:before {
  left: -10px;
}

.md_arrow.right.long:hover:after {
  left: 60px;
}

.md_arrow.totop {
  transform: rotate(180deg);
}

@media all and (-ms-high-contrast: none) {
  .md_arrow.totop {
    left: -10px;
    top: -10px;
  }
}

a.md_arrow__btn, .md_arrow__btn {
  position: relative;
  display: block;
  text-decoration: none;
  color: #444;
  display: inline-block;
  box-sizing: border-box;
  line-height: 1;
  white-space: nowrap;
}

a.md_arrow__btn:before, .md_arrow__btn:before {
  content: '';
  position: absolute;
  margin-top: -1px;
  bottom: -2px;
  left: 0;
  margin-left: 0;
  width: 100%;
  height: 2px;
  background: #000;
  transition: all 0.1s ease-in;
}

a.md_arrow__btn:after, .md_arrow__btn:after {
  content: '';
  position: absolute;
  bottom: -5px;
  left: 100%;
  margin-left: -4px;
  background: url(../images/arrow_blk.svg) 0 0 no-repeat;
  width: 8px;
  height: 8px;
  transform: rotate(90deg);
  transition: all 0.1s ease-in;
}

a.md_arrow__btn:hover:before, .md_arrow__btn:hover:before {
  width: 110%;
}

a.md_arrow__btn:hover:after, .md_arrow__btn:hover:after {
  left: 110%;
}

