    width: 10vw;
  }
}

.md_header.active {
  animation: md_header .75s ease 3s 1 normal forwards;
}

.md_header:after {
  content: '';
  position: absolute;
  top: 0;
  left: -35vw;
  width: 60vw;
  transform: skew(15deg, 0);
  height: 100%;
  background: #fff;
  z-index: -1;
